import Vue from 'vue'
import VueRouter from 'vue-router'
import { getAuth, onAuthStateChanged } from 'firebase/auth'
import Login from '@/views/Login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/secure/orders/login',
    alias: '/',
    name: 'login',
    component: Login,
    meta: { requiresAuth: false },
  },
  {
    path: '/secure/orders/home',
    name: 'home',
    component: () => import(/* webpackChunkName: "home" */ '@/views/Home.vue'),
    meta: { requiresAuth: true },
  },
  {
    path: '/secure/orders/home/monitor-de-ordenes',
    name: 'ordenes',
    component: () =>
      import(/* webpackChunkName: "ordenes" */ '@/views/Ordenes.vue'),
    meta: { requiresAuth: true },
  },
  {
    path: '/secure/orders/home/monitor-de-ordenes/detalles',
    name: 'ordenes-detalle',
    component: () =>
      import(
        /* webpackChunkName: "detalles-ordenes" */ '@/views/OrdenesDetalle.vue'
      ),
    meta: { requiresAuth: true },
  },
  {
    path: '*',
    name: 'not-found',
    component: () =>
      import(/* webpackChunkName: "NotFound - 404" */ '@/views/NotFound.vue'),

    meta: { pageNotFound: false },
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

router.beforeEach((to, from, next) => {
  const auth = getAuth()
  onAuthStateChanged(auth, (user) => {
    if (to.matched.some((record) => record.meta.requiresAuth) && !user) {
      next('/secure/orders/login')
    }
    if (
      to.matched.some((record) => !record.meta.requiresAuth) &&
      user &&
      to.matched.some((record) => record.meta.pageNotFound)
    ) {
      next('/secure/orders/home')
    }
    next()
  })
})

export default router
